import React from "react";
import { Layout, LayoutWithSidebar } from '../components/Layout';
import { IHeaderProps } from '../components/Header';
import { Feature } from "../components/Feature";
import { CookieService } from '../services/cookieService';
import { Features } from "../misc/features";


export class FeaturesPage extends React.Component
{
  constructor(props)
  {
    super(props);
    CookieService.setCookie("lastFeaturesVisit", new Date().toISOString())
  }
  render()
  {
    let header: IHeaderProps = {
      url: "/",
      type: "website",
      title: "Power Query Formatter | Beautify your PowerQuery code",
      description: "What's new | Features"
    };
    
    return (
      <LayoutWithSidebar header={header}>
        {
          Features.map((f,i) => (
            <Feature key={i} date={f.date} title={f.title}>
              {f.content}
            </Feature>
          ))
        }
      </LayoutWithSidebar>
    );
  }
}

export default FeaturesPage;